*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
} 
h2{
	padding:0;
	margin:0;
}
body{
	overflow-x:hidden;
}
header{
	&.head_blue{
		background:$blue2;
		&:before{
			display:none;
		}
	}
	background-size:cover !important;
	float:left;
	width:100%;
	position:relative;
	&:before{
		content:'';
		position:absolute;
		background: url('../images/elipse.png');
		width:116px;
		height:83px; 
		left:50%;
		bottom:-41.5px;
		margin-left:-58px;
