.hot-it-works{
	padding-top:130px;
	background: #fbfafa;
	padding-bottom:100px;
	 
	&__blocks{
		width:100%;
		margin-bottom:80px;
		&__inner{
			display:block;
			width:280px;
			height:280px;
			margin:0 auto;
			box-sizing:border-box;
			text-align:center;
			background:white;
			border-radius:50%;
			padding-top:60px; 
			&__img{
				height:70px;
				img{
					vertical-align:bottom;
				}
			}
			&__text{  
				text-align:center;
				display:flex;
			    justify-content: center;
				p{
					display: flex;
					align-items: flex-end; 
					text-align:center;
					text-transform:uppercase;
					color:black;
					font-size:18px;
					font-family: 'CormorantGaramond-Light';
					position:relative;
					text-align:center; 
					height:65px;
					padding-bottom:25px;
					&:before{
						content:'';
						position:absolute;
						left:50%;
						bottom:0;
						width:44px;
						height:1px;
						background:$blue2;
						transition:0.3s all;
					}
					&:after{
						content:'';
						position:absolute;
						right:50%;
						bottom:0;
						width:44px;
						height:1px;
						background:$blue2;
						transition:0.3s all;
					}
				}
			}
			&:hover{
				p{
					&:before{
						width:66px;
					}
					&:after{
						width:66px;
					}
				}
			}
		}
	}
}
