.ceremony{
	margin-bottom:100px;
	&__slider{
		&__block{
			text-align:center;
			padding:0 20px;
			margin-bottom:80px;
			&__img{
				height:450px;
				position:relative;
				overflow:hidden;
				margin-bottom:30px;
				&:before{
					content:'';
					position:absolute;
					height:90%;
					width:90%;
					left:5%;
					top:5%;
					z-index:1;
					border:1px solid $blue;
				}
				&:hover img{
					transform: scale(1.05);
				}
				img{
					height:100%;
					width:100%;
					object-fit:cover;
					transition:0.6s all;
				}
			}
			&__title{
				font-size:21px;
				font-family: 'Lato-Light';
			}
			&__text{
				p{
					font-size:16px;
					line-height:1.6;
					font-family: 'Lato-Light';
				}
			}
			&__link{
				a{
					background:$blue2;
					color:white;
					padding:15px 21px;
				}
			}
		}
	}
	.owl-carousel { 
		position:relative;
		.owl-nav {
		position:absolute;
		top:50%;
		margin-top:-30px;
		width:100%;

			.owl-next, 
			.owl-prev{
				height:60px;
				width:60px;
				border-radius:50%;
				border:1px solid $blue2;
				transition:0.3s all;
				&:hover{
					background:$blue2;
				}
			}
			.owl-prev{
				float:left;
				margin-left:-75px;
				position:relative;
				&:before{
					content:'';
					position:absolute;
					left:50%;
					margin-left:- 4.5px;
					top:50%;
					margin-top:-7px;
					width:9px;
					height:14px;
					background: url('../images/slider-arrows.png') no-repeat;
				}
				&:hover::before{
					background: url('../images/slider-arrows.png') no-repeat left bottom;
				}
			}
			.owl-next{
				float:right;
				margin-right:-75px;
				position:relative;
				&:before{
					content:'';
					position:absolute;
					left:50%;
					margin-left:- 4.5px;
					top:50%;
					margin-top:-7px;
					width:9px;
					height:14px;
					background: url('../images/slider-arrows.png') no-repeat right top;
				}
				&:hover::before{
					background: url('../images/slider-arrows.png') no-repeat right bottom;
				}
			}
		}
	}
} 
