.info-search{
	&__search{
		width:100%;
		form{
			margin-left:16.66667%;
			position:relative;
			margin-bottom:60px;
			input[type='text']{
				height:41px;
				box-sizing:border-box;
				border:1px solid #e5e5e5;
				transition:0.3s all;
				padding-left:36px;
				width:100%;
				&:focus{
					outline:none;
					border-color:#e5e5e5 - 70%;
				}
			}
			input[type='submit']{
				height:41px;
				width:72px;
				box-sizing:border-box;
				background: url('../images/search_ic.png')no-repeat center, $blue2;
				border-radius:0 20px 20px 0;
				border:1px solid $blue2;
				position:absolute;
				transition:0.3s all;
				right:0;
				top:0;
				&:hover{
					background: url('../images/search_ic_h.png')no-repeat center, white;
				}
			}
		}
		&__find{ 
			display:none;
			position:absolute;
			left:15px;
			top:100%;
			padding:0;
			width: calc(100% - 90px);
			border:1px solid #e5e5e5;
			border-radius: 0 0 15px 15px;
			z-index:100;
			background:white;
			li{
				display:block;
				padding:0 17px;
				a{
					padding:17px;
					display:block;
					border-bottom:1px solid #e5e5e5;
					text-decoration:none;
					transition:0.3s all;
					color:rgba(0,0,0,0.3); 
					&:hover{
						color:black;
					}
				}
					&:last-child a{
						border-bottom:0;
					}
			}
		}
	}
	.info__block-left__text{
		border:1px solid #e9e9e9;
	}
}
.info-search__search form input[type='text']:focus ~ .info-search__search__find{
	display:block;
}
