.menu_d{
	display:block;
}
.menu_m{
	display:none;
}
.mob_d{ 
	display:none;
}
.dest_d{
	display:block; 
}
.menu.menu_m{
	position:absolute;
	left:0;
	top:0;
	width:100%; 
	background:rgba(0,0,0,0.9); 
	padding: 20px 0 40px;
	text-align:center;
	z-index:100;
	margin:0;
	transition:0.3s all;
	transform:translate3d(0px,-1000px,0px); 
	li{
	padding: 20px 0;
		display:block;
	}
}
.menu.menu_m.active{
	transform:translate3d(0px,-0px,0px); 
}
.menu-but.menu_m{
	height:40px;
	width:40px;
	position:relative;
	float:right;
		cursor:pointer;
		margin-top:20px;
	span{
		width:100%;
		height:4px;
		display:block;
		background:white;
		margin-bottom:10px;
	}
}
.comments__block__right.row.dest_d{
	display:flex;
}
